.p_blog_archive_list_container {
  position: relative;
  .p_blog_archive_list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    grid-column-gap: 32px;
    grid-row-gap: 45px;
  }
  .p_blog_item {
    position: relative;
    display: block;
    padding: 0;
    margin: 0;
    .post_date {
      position: relative;
      font-weight: normal;
      font-size: 14px;
      letter-spacing: 0.1em;
      color: #999999;
      line-height: 26px;
      padding-bottom: 6px;
    }
    &_img {
      position: relative;
      width: 100%;
      img {
        position: relative;
        width: 100%;
        height: 230px;
      }
    }
    &_info {
      position: relative;
      padding-top: 14px;
    }
    .post_title {
      position: relative;
      font-weight: normal;
      font-size: 16px;
      line-height: 26px;
      color: #000000;
      width: 100%;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      margin-bottom: 6px;
    }
    .post_text {
      position: relative;
      font-weight: normal;
      font-size: 14px;
      line-height: 26px;
      color: #666666;
    }
    &_link {
      position: relative;
      display: block;
      padding: 0;
      margin: 0;
      text-decoration: none;
      outline: none;
      .p_blog_item_img {
        overflow: hidden;
        img {
          transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.7s;
        }
      }
      &:hover {
        .p_blog_item_img {
          img {
            transform: scale(1.15);
          }
        }
        .post_title {
          text-decoration: underline;
        }
      }
    }
    .more_link_wrap {
      position: relative;
      padding-top: 8px;
      .more_link {
        position: relative;
        font-weight: normal;
        letter-spacing: 0.1em;
        font-size: 14px;
        line-height: 26px;
        color: #32A7E0;
        text-decoration: none;
        transition: all 0.2s;
        &::before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background: #32A7E0;
        }
        &:hover {
          &::before {
            width: 100%;
            animation: link-lineloop 0.7s ease-out;
            -webkit-animation: link-lineloop 0.7s ease-out;
          }
        }
      }
    }
  }
}

.p_blog_single {
  position: relative;
  .single_main {
    position: relative;
    max-width: 700px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    @include media('sp') {
      margin-bottom: 20px;
    }
    .date {
      position: relative;
      display: block;
      font-family: $font_roboto;
      font-weight: 300;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0.05em;
      text-align: left;
      color: #FFFFFF;
      margin-bottom: 2px;
    }
    .title {
      position: relative;
      font-weight: bold;
      font-size: 20px;
      line-height: 30px;
      color: #FFFFFF;
      margin-bottom: 20px;
      @include media('sp') {
        font-size: 16px;
        line-height: 28px;
      }
    }
    .thumb {
      position: relative;
      width: 100%;
    }
  }
  .single_content {
    position: relative;
    max-width: 700px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  .single_content {
    position: relative;
    font-family: $font_noto;
    font-size: 14px;
    line-height: 1.6;
    font-weight: 300;
    color: #FFFFFF;
    @include media('sp') {
      font-size: 12px;
      line-height: 1.8;
    }
    h1, h2, h3 {
      position: relative;
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      margin-top: 16px;
      margin-bottom: 16px;
      @include media('sp') {
        font-size: 16px;
      }
    }
    h4 {
      position: relative;
      font-weight: bold;
      font-size: 18px;
      line-height: 1.6;
      margin-top: 16px;
      margin-bottom: 16px;
      @include media('sp') {
        font-size: 14px;
      }
    }
    h5, h6 {
      position: relative;
      font-weight: bold;
      font-size: 16px;
      line-height: 1.6;
      margin-top: 16px;
      margin-bottom: 16px;
      @include media('sp') {
        font-size: 14px;
      }
    }
    p {
      position: relative;
      font-size: 14px;
      line-height: 1.6;
      font-weight: 300;
      margin-top: 16px;
      margin-bottom: 16px;
      @include media('sp') {
        font-size: 12px;
      }
    }
    figure {
      position: relative;
      width: 100%;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    ul {
      position: relative;
      list-style: disc;
      padding-left: 16px;
      margin-top: 16px;
      margin-bottom: 16px;
      @include media('sp') {
        padding-left: 14px;
      }
    }
    strong, b {
      font-weight: bold;
    }
    i {
      font-style: italic;
    }
    a {
      position: relative;
      color: inherit;
      text-decoration: underline;
    }
    img {
      max-width: 100%;
      display: block;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }
}

