.p_single_pagination {
  position: relative;
  display: block;
  margin-top: 40px;
  &::after {
    content: "";
    visibility: hidden;
    height: 0;
    display: block;
    font-size: 0;
    clear: both;
  }
  .page_btn {
    position: relative;
    font-family: $font_noto;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0;
    color: #FFFFFF;
    text-align: center;
    padding: 10px 16px;
    min-width: 90px;
    text-decoration: none;
    outline: none;
    border: 1px solid #999999;
    transition: all 0.3s;
    @include media('pc') {
      &:hover {
        background-color: #FFFFFF;
        border-color: #FFFFFF;
        color: #000000;
      }
    }
    @include media('sp') {
      font-size: 12px;
      line-height: 18px;
      min-width: 70px;
    }
    &.prev_btn {
      float: left;
    }
    &.next_btn {
      float: right;
    }
  }
}