.c_top_fv_section {
  position: relative;
  padding: 0;
  margin: 0;
  overflow: hidden;
  .c_top_fv_img {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    img {
      position: relative;
      width: 100%;
      height: 100%;
    }
  }
  .c_top_main_slider {
    position: relative;
    z-index: 0;
  }
  .c_top_main_title {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
    @include media('sp') {
      padding: 30px 0;
    }
    .inner {
      position: relative;
      flex: 0 0 100%;
      width: 100%;
    }
    .container {
      max-width: 1100px;
      padding: 0;
    }
    .title_picture {
      position: relative;
      width: 65.5rem;
      max-width: 100%;
      margin-right: auto;
      margin-left: 0;
      @include media('lb') {
        width: 40.5rem;
      }
      @include media('sp') {
        width: 230px;
      }
    }
  }
}

.c_top_main_slider {
  position: relative;
  .c_main_slide_item {
    position: relative;
    padding: 0;
    .c_main_slide_img {
      position: relative;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        height: clamp(400px, 46.5vw, 800px);
        @include media('sp') {
          height: 375px;
        }
      }
    }
    .c_main_slide_video {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      object-fit: cover;
      width: 100%;
      height: 100%;
      height: clamp(400px, 46.5vw, 800px);
      @include media('sp') {
        height: 375px;
      }
    }
  }
  .slick-list {
    position: relative;
  }
  .slick-slide {
    position: relative;
  }
  .slide-animation{
    .c_main_slide_img {
      animation: fadezoom 8s 0s forwards;
    }
  }
}

.p_fv_section {
  position: relative;
  padding: 0;
  margin: 0;
  background-image: url(../img/common/pageindex.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 300px;
  @include media('sp') {
    height: 198px;
  }
  .container {
    position: relative;
    height: 100%;
  }
  .p_fv_info {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 30px 0;
    height: 100%;
    .info_wrap {
      position: relative;
      flex: 0 0 100%;
      width: 100%;
    }
    .lead_title {
      position: relative;
      font-weight: 900;
      font-size: 34px;
      letter-spacing: 0.2em;
      line-height: 46px;
      text-align: center;
      color: #000000;
      margin-bottom: 6px;
      @include media('sp') {
        font-size: 24px;
        letter-spacing: 0.1em;
        line-height: 32px;
        margin-bottom: 2px;
      }
    }
    .sub_title {
      position: relative;
      font-family: $font_ubuntu;
      font-weight: bold;
      font-style: italic;
      font-size: 18px;
      letter-spacing: 0.1em;
      line-height: 32px;
      text-align: center;
      color: #000000;
      padding: 0;
      margin: 0;
      @include media('sp') {
        font-size: 16px;
        line-height: 28px;
        letter-spacing: 0.1em;
      }
    }
  }
}

.p_breadcrumbs_section {
  position: relative;
  padding: 18px 0;
  background: #000000;
  margin: 0;
  border-top: 1px solid #B9B9B9;
  border-bottom: 1px solid #808080;
  ol {
    position: relative;
    list-style: none;
    display: block;
    font-size: 14px;
    line-height: 24px;
    text-align: left;
    color: #FFFFFF;
    padding: 0;
    @include media('sp') {
      font-size: 12px;
      line-height: 20px;
    }
    li {
      position: relative;
      display: inline;
      padding: 0;
      margin: 0;
      & + li {
        &::before {
          content: ">";
          position: relative;
          display: inline-block;
          padding-left: 10px;
          padding-right: 10px;
        }
      }
    }
    a {
      color: #FFFFFF;
      text-decoration: none;
      outline: none;
      font-family: inherit;
      font-size: inherit;
      text-decoration: underline;
      &:hover {
        opacity: 0.7;
      }
    }
  }
}

.top_news_section {
  position: relative;
  padding: 12px 0;
  background: #000000;
  .top_news_inner {
    position: relative;
    display: grid;
    grid-template-columns: 96px minmax(0px, 1fr);
    grid-column-gap: 28px;
    @include media('sp') {
      grid-template-columns: 72px minmax(0px, 1fr);
      grid-column-gap: 16px;
    }
    .inner_left {
      position: relative;
      border-right: 1px solid #808080;
    }
    .inner_right {
      position: relative;
    }
  }
  .news_ttl {
    position: relative;
    font-weight: 900;
    font-size: 16px;
    letter-spacing: 0.1em;
    line-height: 24px;
    text-align: left;
    color: #fff;
    margin-bottom: 4px;
  }
  .viewmore {
    position: relative;
    .morelink {
      position: relative;
      display: inline-block;
      font-size: 12px;
      line-height: 18px;
      letter-spacing: 0.1em;
      font-weight: normal;
      color: #FFFFFF;
      text-decoration: none;
      &::after {
        content: "";
        position: absolute;
        bottom: 1px;
        left: 0;
        display: inline-block;
        width: 100%;
        height: 1px;
        background: #FFFFFF;
      }
      &:hover {
        &::after {
          animation: link-lineloop 0.6s ease-out;
        }
      }
    }
  }
  .news_list {
    position: relative;
    list-style: none;
    width: 100%;
    max-width: 100%;
    padding: 2px 0;
    & > li {
      position: relative;
      display: block;
      width: 100%;
      max-width: 100%;
      &:not(:last-child) {
        margin-bottom: 3px;
      }
    }
    .news_item {
      position: relative;
      width: 100%;
      max-width: 100%;
      display: grid;
      grid-template-columns: 90px minmax(0px, 1fr);
      grid-column-gap: 10px;
      font-weight: normal;
      color: #FFFFFF;
      @include media('sp') {
        grid-template-columns: 78px minmax(0px, 1fr);
        grid-column-gap: 6px;
      }
      .date {
        position: relative;
        display: block;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 0.1em;
        color: #FFFFFF;
        @include media('sp') {
          letter-spacing: 0;
        }
      }
      .ttl {
        position: relative;
        display: block;
        max-width: 100%;
        font-family: $font_noto;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 0.1em;
        white-space: nowrap;
        width: 100%;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #FFFFFF;
        @include media('sp') {
          letter-spacing: 0;
        }
        a {
          display: inline;
          color: inherit;
          text-decoration: none;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}

