      margin-left: auto;
      margin-right: auto;
    }
    & > li {
      position: relative;
    }
    .feature_item {
      position: relative;
      width: 100%;
      height: 100%;
      background: #0A5046;
      padding: 28px 30px 30px;
      z-index: 0;
      @include media('sp') {
        padding: 28px 20px 30px;
      }
      .icon {
        position: relative;
        width: 40px;
        height: 40px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      .title {
        position: relative;
        font-family: $font_mukta;
        font-weight: 800;
        font-size: 16px;
        letter-spacing: 0.05em;
