        text-align: center;
        color: #FFFFFF;
        padding-bottom: 12px;
        border-bottom: 1px dashed #FFFFFF;
        margin-bottom: 16px;
      }
      .content {
        position: relative;
        .lead {
          position: relative;
          font-weight: bold;
          font-size: 18px;
          letter-spacing: 0.1em;
          line-height: 30px;
          text-align: center;
          color: #FFFFFF;
          width: fit-content;
          max-width: 100%;
          margin-left: auto;
          margin-right: auto;
