.top_point_list_section {
  position: relative;
  padding: 70px 0;
  background: #F7F7F7;
  @include media('sp') {
    padding: 60px 0;
  }
  .point_main_ttl {
    position: relative;
    padding: 10px 16px;
    background: #FFFFFF;
    border: 1px solid #000000;
    border-radius: 20px;
    font-weight: 900;
    font-size: 24px;
    letter-spacing: 0.1em;
    line-height: 36px;
    text-align: center;
    color: #000000;
    margin-bottom: 50px;
    @include media('sp') {
      font-size: 18px;
      line-height: 30px;
      letter-spacing: 0.05em;
      border-radius: 12px;
    }
    small {
      font-size: 12px;
      line-height: 24px;
      font-weight: normal;
    }
  }
  .point_list {
    position: relative;
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, minmax(0px, 1fr));
    grid-column-gap: 36px;
    grid-row-gap: 30px;
    counter-reset: point;
    margin-bottom: 70px;
    @include media('tb') {
      grid-column-gap: 30px;
    }
    @include media('sp') {
      grid-template-columns: 100%;
      grid-column-gap: 20px;
      grid-row-gap: 40px;
      margin-bottom: 50px;
    }
    & > li {
      position: relative;
    }
    .point_item {
      position: relative;
      .thumb {
        position: relative;
        width: 100%;
      }
      .content {
        position: relative;
        padding: 18px 0 0;
        .ttl {
          position: relative;
          padding-left: 62px;
          min-height: 56px;
          display: flex;
          align-items: center;
          font-weight: 900;
          font-size: 20px;
          letter-spacing: 0.1em;
          line-height: 28px;
          text-align: left;
          color: #000;
          margin-bottom: 16px;
          @include media('sp') {
            font-size: 18px;
          }
          &::before {
            counter-increment: point;
            content: counter(point);
            position: absolute;
            top: 4px;
            left: 0;
            display: inline-block;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: 1px solid #000000;
            font-family: $font_anton;
            font-weight: normal;
            font-size: 30px;
            letter-spacing: 0;
            line-height: 1;
            text-align: center;
            color: #000;
            padding: 10px;
          }
        }
        .txt {
          position: relative;
          font-size: 16px;
          line-height: 24px;
          font-weight: normal;
          color: #000;
          @include media('sp') {
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 0.1em;
          }
        }
        .help {
          position: relative;
          font-size: 14px;
          line-height: 24px;
          font-weight: normal;
          color: #000;
          margin-top: 6px;
          @include media('sp') {
            font-size: 12px;
            line-height: 20px;
            letter-spacing: 0.1em;
            margin-top: 10px;
          }
        }
      }
    }
  }
  .point_alert {
    position: relative;
    padding: 24px 16px;
    background: #FFFFFF;
    border: 1px dashed #999999;
    margin-bottom: 16px;
    @include media('sp') {
      padding: 20px 16px;
    }
    .lead {
      font-weight: normal;
      font-size: 18px;
      letter-spacing: 0.1em;
      line-height: 2;
      text-align: center;
      color: #4D4D4D;
      @include media('sp') {
        font-size: 16px;
        line-height: 28px;
      }
    }
  }
  .point_help {
    position: relative;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: 0.1em;
    line-height: 28px;
    text-align: center;
    color: #666666;
    @include media('sp') {
      font-size: 14px;
      line-height: 24px;
    }
  }
  .point_notice {
    position: relative;
    padding: 24px 16px;
    background: #A10707;
    border-radius: 10px;
    margin-top: 40px;
    @include media('sp') {
      padding: 20px 16px;
    }
    .lead {
      font-weight: 900;
      font-size: 24px;
      letter-spacing: 0.1em;
      line-height: 36px;
      text-align: center;
      color: #FFFFFF;
      @include media('sp') {
        font-size: 18px;
        line-height: 30px;
      }
    }
  }
}

.top_service_list_section {
  padding: 70px 0;
  background: #111111;
  @include media('sp') {
    padding: 60px 0;
  }
  .service_list {
    position: relative;
    list-style: none;
    padding: 0;
    & > li {
      position: relative;
      &:not(:last-child) {
        margin-bottom: 70px;
        @include media('sp') {
          margin-bottom: 60px;
        }
      }
    }
    .service_item {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      & > .left {
        position: relative;
        flex: 0 0 auto;
        width: calc(50% - 15px);
        @include media('sp') {
          width: 100%;
          margin-bottom: 20px;
        }
      }
      & > .right {
        position: relative;
        flex: 0 0 auto;
        width: calc(50% - 15px);
        padding-right: calc( ( var(--vw, 1vw) * 50 ) - 508px );
        @include media('max') {
          padding-right: 32px;
        }
        @include media('sp') {
          width: 100%;
          padding: 0 16px;
        }
      }
      .thumb {
        position: relative;
        width: 100%;
      }
      .index {
        position: relative;
        display: block;
        padding: 12px 24px 12px 20px;
        background: #FFFFFF;
        transition: all 0.4s ease-in-out;
        z-index: 0;
        margin-bottom: 20px;
        @include media('sp') {
          padding: 10px 24px 10px 20px;
          margin-bottom: 16px;
        }
        .lead {
          position: relative;
          font-weight: bold;
          font-size: 24px;
          letter-spacing: 0.1em;
          line-height: 36px;
          text-align: left;
          color: #000;
          z-index: 0;
          @include media('sp') {
            font-size: 18px;
            line-height: 28px;
          }
        }
        .sub {
          position: relative;
          font-weight: 300;
          font-size: 14px;
          letter-spacing: 0.1em;
          line-height: 24px;
          text-align: left;
          color: #000;
          z-index: 0;
          @include media('sp') {
            line-height: 23px;
          }
        }
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 4px;
          background: #A10707;
          z-index: 1;
        }
        &::after {
          content: "";
          position: absolute;
          top: 50%;
          right: 14px;
          display: inline-block;
          width: 10px;
          height: 10px;
          background-image: url(../img/common/icon-play.png);
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          transform: translateY(-50%);
          z-index: 2;
        }
        &:hover {
          opacity: 0.7;
        }
      }
      .content {
        position: relative;
        padding: 0 10px;
        @include media('sp') {
