          .sub {
            position: relative;
            font-weight: 900;
            font-size: 20px;
            letter-spacing: 0.1em;
            line-height: 30px;
            text-align: center;
            color: #fff;
            @include media('sp') {
              font-size: 16px;
              line-height: 24px;
            }
          }
        }
        .box_body {
          position: relative;
          padding: 16px 12px;
          background: #333333;
          .txt {
            position: relative;
            font-weight: bold;
            font-size: 16px;
            letter-spacing: 0.03em;
            line-height: 28px;
            text-align: center;
            color: #fff;
            @include media('sp') {
              font-size: 14px;
              line-height: 24px;
            }
          }
        }
      }
      .thumb_list {
        position: relative;
        list-style: none;
        padding: 0;
        display: grid;
        grid-template-columns: repeat(2, minmax(0px, 1fr));
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        & > li {
          position: relative;
          &:nth-child(1) {
            grid-column: 1 / 3; 
          }
        }
        .thumb {
          position: relative;
          width: 100%;
        }
      }
    }
  }
}

.p_company_table {
  position: relative;
  width: 100%;
  border-collapse: collapse;
  font-family: $font_noto;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 300;
  color: #000000;
  text-align: left;
  background-color: #FFFFFF;
