  @include media('sp') {
    font-size: 12px;
  }
  th {
    position: relative;
    padding: 16px 20px;
    background: #CCCCCC;
    text-align: center;
    border: 1px solid #999999;
    width: 24%;
    @include media('sp') {
      padding: 12px 16px;
      width: 29%;
    }
  }
  td {
    position: relative;
    padding: 16px 20px;
    border: 1px solid #999999;
    @include media('sp') {
