    }
  }
}

.p_access_map {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  iframe {
    width: 100%;
  }
}

.p_contact_intro {
  position: relative;
  .inner_row {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    .inner_col {
      position: relative;
      flex: 0 0 auto;
      width: 50%;
      padding-left: 10px;
      padding-right: 10px;
      @include media('sp') {
        width: 100%;
        &:not(:last-child) {
          margin-bottom: 20px;
        }
      }
    }
  }
  .lead {
    position: relative;
    font-size: 16px;
    line-height: 28px;
    font-weight: 900;
    color: #FFFFFF;
    margin-bottom: 10px;
    @include media('sp') {
      font-size: 14px;
      line-height: 24px;
      text-align: center;
    }
  }
  .txt {
    position: relative;
    font-size: 14px;
    line-height: 24px;
    font-weight: normal;
    color: #FFFFFF;
    @include media('sp') {
      font-size: 12px;
      line-height: 20px;
      text-align: center;
    }
  }
  .phone {
    position: relative;
    margin-top: 16px;
    line-height: 1;
    @include media('sp') {
      text-align: center;
      margin-top: 16px;
    }
    .link {
      position: relative;
      display: inline-block;
      font-family: $font_roboto;
      font-size: 34px;
      line-height: 1.2;
      letter-spacing: 0.1em;
      font-weight: 500;
      color: #FFFFFF;
      text-decoration: none;
      outline: none;
      @include media('sp') {
        font-size: 30px;
      }
      &:hover {
        color: #999999;
      }
    }
  }
  .qr_code {
    position: relative;
    max-width: 116px;
    width: 100%;
    @include media('sp') {
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.p_contact_form {
  position: relative;
  font-size: 14px;
  line-height: 24px;
  font-weight: normal;
  .lead_help {
    position: relative;
    margin-bottom: 20px;
    @include media('sp') {
      margin-bottom: 16px;
    }
  }
  .form_group {
    position: relative;
    display: grid;
    grid-template-columns: 200px calc(100% - 220px);
    grid-column-gap: 20px;
    margin-bottom: 20px;
    width: 100%;
    @include media('sp') {
      grid-template-columns: 100%;
      grid-row-gap: 8px;
      margin-bottom: 16px;
    }
    .label {
      position: relative;
      padding: 10px 0;
      @include media('sp') {
        padding: 0;
      }
      .required {
        position: relative;
        color: #FF0000;
        margin-left: 6px;
      }
    }
    .input {
      position: relative;
      width: 100%;
      input, textarea, select {
        vertical-align: middle;
      }
    }
  }
  .s {
    width: 150px;
    max-width: 100%;
  }
  .m {
    width: 200px;
    max-width: 100%;
  }
  .form_tel_row {
    display: flex;
    align-items: center;
    .wpcf7-form-control-wrap {
      flex: 0 0 auto;
      width: auto;
      max-width: calc((100% - 56px) / 3);
    }
    input {
      width: 150px;
      max-width: 100%;
    }
    .line {
      position: relative;
      display: inline-block;
      width: 28px;
      height: 40px;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        display: inline-block;
        width: 10px;
        height: 1px;
        background: #858585;
        transform: translateX(-50%);
      }
    }
  }
  .form-check {
    position: relative;
    margin-top: 20px;
    text-align: center;
    @include media('sp') {
      margin-top: 16px;
    }
  }
  .form-submit {
    position: relative;
    margin-top: 30px;
    @include media('sp') {
      margin-top: 20px;
    }
    .btn {
      position: relative;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.p_privacy_list {
  position: relative;
  list-style: none;
  padding: 0;
  & > li {
    position: relative;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  .ttl {
    position: relative;
    font-size: 16px;
    line-height: 28px;
    font-weight: bold;
    padding-bottom: 10px;
    border-bottom: 1px dotted #CCCCCC;
    margin-bottom: 12px;
  }
  .m_txt {
    font-size: 14px;
    line-height: 24px;
  }
  ul.m_txt {
    position: relative;
    list-style: none;
    padding: 0;
    counter-reset: list;
    & > li {
      position: relative;
      padding-left: 16px;
      @include media('sp') {
        padding-left: 14px;
      }
      &::before {
        counter-increment: list;
        content: counter(list)'.';
        position: absolute;
        top: 0;
        left: 0;
        color: #A10707;
      }
    }
  }
}